// 微信相关的方法
const wx = window.wx || {}
export var isLogin = false
export const initWechatJs = (appId, nonceStr, timestamp, signature, router) => {
  if (isLogin === false) {
    wx.config({
      debug: false,
      appId: appId,
      timestamp: timestamp,
      nonceStr: nonceStr,
      signature: signature,
      jsApiList: [
        'checkJsApi',
        'chooseImage',
        'uploadImage',
        'onMenuShareTimeline',
        'getLocalImgData'
      ]
    })
  }
  isLogin = true
  wx.ready(function () {
    // wx.showOptionMenu()
    if (router === '/sharephoto') {
      wx.onMenuShareTimeline({
        title: `自定义标题`,
        link: 'http://coach.realmshow.com?isLast=1',
        imgUrl: `http://citic365.e-mallchina.com.cn/image/shareImg.jpg`
      })
      wx.onMenuShareAppMessage({
        title: `自定义标题`, // 分享标题
        desc: `自定义描述`, // 分享描述
        link: 'http://coach.realmshow.com?isLast=1', // 分享链接
        imgUrl: `http://citic365.e-mallchina.com.cn/image/shareImg.jpg` // 分享图片
      })
    } else {
      wx.onMenuShareTimeline({
        title: `自定义标题`,
        link: 'http://coach.realmshow.com',
        imgUrl: `http://citic365.e-mallchina.com.cn/image/shareImg.jpg`
      })
      wx.onMenuShareAppMessage({
        title: `自定义标题`, // 分享标题
        desc: `自定义描述`, // 分享描述
        link: 'http://coach.realmshow.com', // 分享链接
        imgUrl: `http://citic365.e-mallchina.com.cn/image/shareImg.jpg` // 分享图片
      })
    }
  })
}

export const openCameraForPic = function (type, uploadCallback) {
  var sourceType = []
  if (type === 1) {
    sourceType.push('album')
  } else {
    sourceType.push('camera')
  }
  wx.chooseImage({
    count: 1, // 默认9
    sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
    sourceType: sourceType, // 可以指定来源是相册还是相机，默认二者都有
    success: function (res) {
      var localIds = res.localIds // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
      console.log(localIds)
      if (uploadCallback && typeof uploadCallback === 'function') {
        uploadCallback(localIds)
      }
    },
    error: function (e) {
      alert(e)
    }
  })
}



